.content {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  padding: 60px 65px;
  margin: 0;
}

.title {
  text-align: center;
  letter-spacing: 0.35em;
  line-height: 1;
  font-family: "Didact Gothic";
  font-size: 36px;
  color: rgba(70, 69, 83, 1);
  margin: 20px auto;
}

.photos {
  display: flex;
  margin: 45px auto 20px;
  width: 100%;
}

.photoItem {
  overflow: hidden;
  text-align: center;
}

.photoItem img {
  display: inline;
  width: 380px;
  top: 0px;
  left: 0px;
  height: 565px;
  padding: 5px 5px 0;
  overflow: hidden;
  cursor: pointer;
  object-fit: cover;
}

/* */

.gallery {
  -webkit-column-count: 3;
  -moz-column-count: 3;
  column-count: 3;
  padding: 0 12px;
  margin: auto 150px;
}

.gallery .pics {
  -webkit-transition: all 350ms ease;
  transition: all 350ms ease;
  cursor: pointer;
  margin: -1px -5px;
}

.gallery .pics:hover {
  -webkit-transition: all 2s ease;
  transition: all 2s ease;
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

@media (max-width: 991px) {
  .gallery {
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
  }
}

@media (max-width: 480px) {
  .gallery {
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 1;
    -webkit-column-width: 100%;
    -moz-column-width: 100%;
    column-width: 100%;
  }
}

.model {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .8);
  transition: opacity 0.4s ease, visibility 0.4s ease,
    transform 0.5s ease-in-out;
  visibility: hidden;
  transform: scale(0);
  overflow: hidden;
  z-index: 999;
}

.model.open {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}

.model img{
  width: auto;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: 2px solid #a9a9a9;
}

#chevronRight,
#chevronLeft,
#x {
  position: fixed;
  color: #a9a9a9;
  cursor: pointer;
}

#chevronRight {
  right: 70px;
}

#chevronLeft {
  left: 70px;
}

#x {
  top: 15px;
  right: 15px;
  width: 2rem;
  height: 2rem;
  padding: 5px;
}

.model.open img:hover {
  -webkit-transition: all 2s ease;
  transition: all 2s ease;
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  cursor: pointer;
}
